export default {
	install(Vue){
		Vue.mixin({
			data(){
				return {
					phoneReg:/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
					userRules:{
						loginName:{
							rules:[{
								required:true,
								'errorMessage':''
							},{
								validateFunction:this.validateLoginName
							}]
						},
						password:{
							rules:[{
								required:true,
								'errorMessage':''
							}]
						},
						phone:{
							rules:[{
								required:true,
								'errorMessage':''
							},{
								validateFunction:this.validatePhone
							}]
						},
						vCode:{
							rules:[{
								required:true,
								'errorMessage':''
							},{
								validateFunction:this.validateCode
							}]
						},
					}
				}
			},
			methods:{
				//
				validateLoginName(rule,val,data,callback){
					switch(true){
						case val.length < 5:
						callback('')
						default : 
						return true
					}
				},
				//
				validatePhone(rule,val,data,callback){
					switch(true){
						case !this.phoneReg.test(val):
						callback('')
						default : 
						return true
					}					
				},
				validateCode(){
					return true
				}
			}
		})
	}
}