export default ({name, data = {}}) => {
	return new Promise((resolve, reject) => {
		const loading = data.isLoading;
		delete data.isLoading
		//loading
		!loading && uni.showLoading({})
		
		uniCloud.callFunction({
			name,
			data,
			success({
				result
			}) {
				if (result.code === 0) {
					resolve(result.data)
				} else { //
					uni.showToast({
						icon: "none",
						title: result.msg
					})
				}
			},
			fail(err) { //
				reject(err)
			},
			complete() {
				//
				!loading &&uni.hideLoading()
			}
		})
	})
}
